import React, {PureComponent} from 'react'
import style from './shoppingBasketModal.module.scss';
import { ShoppingBasketContext } from '../shoppingBasketProvider/shoppingBasketProvider.js';

export default class ShoppingBasketModal extends PureComponent {

  render() {
    return (
      <div className={style.modalBackground}>
        <div className={style.modal}>
        <button role="img"
          onClick={() => {this.props.closeShoppingModal()}}
          className={style.close}>
          <span role="img" aria-label="close">❌</span>
        </button>
          <div>
            <h3 className={style.title}><b>Корзина</b></h3>
            <div className={style.subtitle}>
              <div className={style.unit} key="1"><b>
                Название:
                </b></div>
              <div className={style.unit} key="2"><b>
                Колличество:
              </b></div>
            </div>
            <div className={style.order}>
            <ShoppingBasketContext.Consumer>
              {( {basketItems} ) => basketItems
                .map((item) => (
                  <div className={style.orderUnit}>
                    <div key="1">
                    <span>
                      {item.name}
                      </span>
                    </div>
                    <div key="2"><span>
                        {item.quantity}
                        </span>
                      <span> шт.</span>
                    </div>
                  </div>
                ))
              }
            </ShoppingBasketContext.Consumer>
            </div>

            <ShoppingBasketContext.Consumer>
              {( {totalPrice} ) =>
                <div className={style.total}>
                  <p><b>
                    Общая стоимость: &nbsp;
                  </b></p>
                  <div>
                    {totalPrice}
                    <span> грн</span>
                  </div>
                </div>
              }
            </ShoppingBasketContext.Consumer>
          </div>
        </div>
      </div>
    )
  }
}

