import React, {PureComponent} from 'react'
import json from '../../components/json/json.js'

export default class AboutUs extends PureComponent {
  // json.find(element => )

    render() {
      const id = Number( this.props.id.match.params.id );
      const item = json.find(element => element.id === id);
            item.img = `../${item.img}`;

        return (
          <div>
            <div><img src={item.img} alt={item.name} width="500px" /></div>
              <h1>{item.name}</h1>
              <p>{item.components}</p>
              <p><b>Масса:</b> <span>{item.mass}</span></p>
              <p><b>Цена:</b> <span>{item.price}</span></p>
          </div>
        )
    }
}
