import React, {PureComponent} from 'react';
import styles from './sort.module.scss';

export default class Sort extends PureComponent {

  option = [
    {value: "cheap to expensive", name: "от дешевых к дорогим"},
    {value: "expensive to cheap", name: "от дорогих к дешевым"}
  ]

  // тут рендерим кнопки фильтров и пакуем в переменную, чтобы удобно вставить
  render() {
    const buttons = this.option.map(({value, name, selected}) => {
      return (
        <option
          key={name}
          value={value}
        >
          {name}
        </option>
      )

    })

    // вставляем кнопки в компонент
    return (
      <div className="input-group mb-3" className={styles.sort}>
        <select
          id="sort"
          className="custom-select"
          value={this.props.sort}
          multiple={false}
          onChange={(event) => {
            this.props.toSort(event)
          }}
        >
          {buttons}
        </select>
      </div>
    )
  }
}
